@import "styles/variables";
@import "styles/mixins";

.wrapper {
  padding: 1.5rem;
  border: 1px solid $colorDarkest;
  border-radius: 2rem;
}

.header {
  display: flex;
  align-items: center;
  height: 5rem;
  border-bottom: 1px solid $colorDarkest;
}

.icon {
  padding: 1rem;
  width: 5rem;
  fill: $colorLightest;
  background-color: $colorDarkest;
}

.name {
  @include h2;

  margin-left: 2rem;
}

.body {
  --size-cell-width: 10rem;
  display: grid;
  grid-template-columns: calc(2 * var(--size-cell-width)) repeat(2, 1fr);
}

.sizes {
  @include resetList;

  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(3, 1fr);
  border-right: 1px solid $colorDarkest;
}

.item {
  display: flex;
  flex-direction: column;
  padding: 1rem;

  &:not(:nth-child(2n)) {
    border-right: 1px solid $colorDarkest;
  }

  &:not(:last-child):not(:nth-last-child(2)) {
    border-bottom: 1px solid $colorDarkest;
  }
}

.key {
  @include fontAccent($colorDarkest);
}

.value {
  @include fontLink($colorDarkest);

  margin: auto 0 0 auto;
  font-size: 1.8rem;
  font-weight: 600;
}

.info {
  padding-left: 3rem;
  border-right: 1px solid $colorDarkest;
}

.model {
  @include h2;

  margin: 0;
}

.label {
  @include fontAccent($colorDarkest);

  margin: 0;
}

.manufacturer {
  @include fontDescriptor;

  display: block;
  margin-top: 3rem;
}

@media print {
  html {
    font-size: 4px;
  }
}
