@import "styles/mixins";

.wrapper {
  padding-top: 5rem;
  padding-bottom: 5rem;
}

.heading {
  @include h1;
}

.template {
  margin: 5rem auto 0 auto;
  width: 60%;
}

.templatePrint {

}

.button {
  @include resetButton;
  @include fontButtonSimple;

  margin-top: 2rem;
  padding: 1rem;
  background-color: $colorHighlightButton;
  transition-duration: $transitionDuration;

  &:hover {
    box-shadow: 0 0 1rem 0 $colorHighlightButton;
  }
}

.print {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(4, 1fr);
  grid-gap: 3rem 10rem;
  padding: 5rem;
}

.printable {
  display: none;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem 5rem;

  @media print {
    display: grid;
  }
}
